@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

.page-toolbar-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  // align-items: center;
  padding-bottom: 16px;

  .title-edit-mode {
    display: flex;
    align-items: center;

    .edit-program-name {
      margin-left: 8px;
    }
  }

  .toolbar-btns {
    display: flex;
    flex-direction: row;
    gap: 8px;

    .custom-btn {
      width: 100px;
    }
  }

  @include respond(compact) {
    padding-bottom: 0;
    // margin: 0 8px 16px 8px;
    .hide-page-title {
      display: none;
    }
  }
}

